
@use '../base/variables' as v; 
@use '../base/mixins' as m; 

  html,
  body {
    position: relative;
    height: 100%;
  }

  body {
    background: black;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: black;
    margin: 0;
    padding: 0;
  }

  body {
    background: #fff;
    font-family: Helvetica Neue, Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: #000;
    margin: 0;
    padding: 0;
  }

  html,
  body {
    position: relative;
    height: 100%;
  }

  

  .swiper {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 20rem;
    height: 32rem;
            img{
                width: 100%;
                height: 100%;
            }
          
  }

  .swiper-slide {
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 18px;
    font-size: 22px;
    font-weight: bold;
    color: #fff;
  }
.swiper-wrapper{
    margin-bottom: 100%;
}
  .swiper-slide:nth-child(1n) {
    background-color: rgba(0, 0, 0, 0);
  }

  .swiper-slide:nth-child(2n) {
    background-color: rgb(0, 140, 255);
  }

  .swiper-slide:nth-child(3n) {
    background-color: rgb(10, 184, 111);
  }

  .swiper-slide:nth-child(4n) {
    background-color: rgb(211, 122, 7);
  }

  .swiper-slide:nth-child(5n) {
    background-color: rgb(118, 163, 12);
  }

  .swiper-slide:nth-child(6n) {
    background-color: rgb(180, 10, 47);
  }

  .swiper-slide:nth-child(7n) {
    background-color: rgb(35, 99, 19);
  }

  .swiper-slide:nth-child(8n) {
    background-color: rgb(0, 68, 255);
  }

  .swiper-slide:nth-child(9n) {
    background-color: rgb(218, 12, 218);
  }

  .swiper-slide:nth-child(10n) {
    background-color: rgb(54, 94, 77);
  }

  .header{
    background-image:url(../img/backgroundc.png);
    background-repeat: no-repeat;
    background-position: center;
    background-size: cover;
    position: relative;
      &:before{
        background-color: #000;
        bottom: 0;
        content: "";
        left: 0;
        opacity: .4;
        position: absolute;
        right: 0;
        top: 0;
      }
  }
  
  .titulo{
    text-align: center;
    color: #fff;
    padding-top: 10rem;
    position: relative;
        p{
            color: #fff7f7;
            padding-bottom: 20rem;
        }
  }
   .oportunidades{
   background-image: url(../img/opor.png);
   background-position: center;
   background-repeat: no-repeat;
   background-size:contain;
   background-attachment: fixed;
   width: 100%;
   height: 20rem;
  
  }
  .contador{
    background-color: #ea4d60;
  }
  .numeros{
        text-align: center;
        position: relative;
        padding: 5rem;
        p{
          color: #ffffff;
            font-size: 10rem;
            margin: 0;
        }
        
    
  }
  
  .tablero{
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    position: relative;
    
      @include m.telefono { 
        flex-direction: column;
      }
  }
  .vida{
    
    padding: 4rem;
    @include m.telefono { 
      display: flex;
      position: sticky;
    top: 0;
    z-index: 1; 
    padding: 2rem 0 0 0;
    }
  }
  .corazones{
    display: flex;
    flex-direction: column;
      @include m.telefono { 
        flex-direction: row;
      }
  }
  .fallos{
    display: flex;
    flex-direction: column;
    font-size: 3rem;
    @include m.telefono { 
      flex-direction: row;
    }
  }
  .fa-solid{
    padding: 0.2rem;
    color:rgb(189, 9, 9);
    font-size: 3rem;
      @include m.telefono { 
        font-size: 2.5rem;
      }
  }
  .fa-regular{
    padding: 0.2rem;
    
    @include m.telefono { 
      font-size: 2.5rem;
      color: rgb(132, 129, 129);
    }
  }
  .cartas{
    display: grid;
    grid-template-columns: repeat(8,1fr);
    gap: 3rem;
   padding: 3rem 0 0 2rem;
    flex-wrap: wrap;
        @include m.tablet { 
            grid-template-columns: repeat(4,1fr);
        }
       @include m.telefono { 
        gap: 1rem;
        padding-left: 0;
       
       }
  }
   .cartasombra{
    transition: opacity 2s;
    img{
        border-radius: 2rem;
    }
    box-shadow: 2px 2px 10px 1px rgb(0 0 0 / 80%);
    border-radius: 2rem;

            &:hover{
                transform: scale(1.1);
                box-shadow: 9px 10px 10px -1px rgb(0 0 0 / 80%);
                border-radius: 2rem;
                transition: 0.3s;
            }
            @include m.telefono { 
                scale: 0.9;
            }
        }



.reintentar{
 text-align: center;
 padding: 8rem;
    button{
      box-shadow:2px 5px 10px -1px rgb(0 0 0 / 80%);
      background-color: grey;
      padding:2rem 3rem;
      border-radius: 1rem;
      border: none;
      transition: 2s;
      font-size: 2.5rem;
          &:hover{
            background-color: #5f0505;
            box-shadow: 9px 10px 10px -1px rgb(0 0 0 / 0%);
            transition: 2s;
            transform: scale(0.9);
          }
          
    }
    @include m.telefono { 
      padding: 4rem;
          button{
            font-size: 1.5rem;
            padding: 1rem 2rem;
          }
    }
}

 
 .elemento-visible {
  transition: opacity 5s;
     opacity: 1;
    
   }
          
   .elemento-oculto {
    transition: opacity 5s;
    opacity: 0;
    
   }
  
.footer{
    margin-top: 4rem;
    background-color:black ;
    width: 100%;
    height: 30rem;
}        
.ocultar-cartas {
  animation: ocultar 2s ease forwards;
}

@keyframes ocultar {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    display: none;
  }
}

